import React from 'react'
import ButtonGoToTop from '../../components/buttomGoToTop'
import screen1 from './img_Hercules/01.jpg'
import screen2 from './img_Hercules/02.jpg'
import screen3 from './img_Hercules/03.jpg'
import screen4 from './img_Hercules/04.jpg'
import screen5 from './img_Hercules/06.jpg'
import screen6 from './img_Hercules/07.jpg'
import screen7 from './img_Hercules/08.jpg'
import logo from './img_Hercules/logo_Hercules.png'

const HerculesPage = () => {

    return (
        <div>
            <div className='artykul'>
                <img className='logoBright' src={logo} alt={"źródło: https://gamesdb.launchbox-app.com/games/images/10245"} title={"Tak wygląda logo gry"}></img>
                <p lang='pl'>Nie każdemu twórcy udaje się przenieść film do świata gry, do tego stopnia, że jest ona mile wspominana do dzisiaj. Jak wygląda sprawa z grą Herkules od Disneya? Postaram się podsunąć wam odpowiedź na to pytanie w tej recenzji.</p>
                <img className='image' src={screen1} alt={"źródło: własne"} title={"Plansza treningowa"}></img>
                <p lang='pl'>Pierwsze co się nam rzuca w oczy to bardzo ładna grafika 2.5D, która jest połączeniem plansz, obiektów w trójwymiarze i postaci jako  płaskich obiektów. Przyjdzie nam zwiedzać <abbr title='Możemy przenosić się w głąb planszy lub przejść "bliżej" ekranu'>wielowarstwowe</abbr> kolorowe lasy, wielopiętrowe zaludnione miasto, latać w przestworzach na wiernym <abbr title='Mityczny koń ze skrzydłami'>pegazie</abbr>, jak również pędzić w głąb ekranu goniąc za szczęściem, <abbr title='Mityczny stwór przypominający człowieka, ale z charakterystyczny jednym okiem'>cyklopem</abbr> czy za głównym złolem.</p>
                <img className='image' src={screen2} alt={"źródło: własne"} title={"Plansza w lesie, piękna, wielowarstwowa i niebezpieczna"}></img>
                <p lang='pl'>W trakcie naszych poczynań towarzyszyć będzie nam przyjemna muzyka, której różne tony będą przygrywać w zależności od danej sytuacji. Za przerywniki filmowe służą tu klipy z filmowego, Disney-owego Herkulesa, które ujawniają historię przed jak i po ukończonym poziomie. Są one w bardzo dobrej jakości i prawdopodobnie zajmują większą część płyty.</p>
                <img className='image' src={screen3} alt={"źródło: własne"} title={"Te małe futrzaki przerażały mnie za młodego jak i dzisiaj"}></img>
                <p lang='pl'>Jak wspomniałem wcześniej, na początku będziemy mieli planszę treningową, gdzie Herkules poznaje swoje umiejętności. W lesie zwiedzimy jego pierwszy, drugi, a nawet trzeci plan. W pięknym mieście odwiedzimy labirynt pięter i zakamarków z <abbr title='Gdzie można znaleźć litery oraz naczynia do zrobienia save-a'>sekretami</abbr>. W między czasie jeszcze pobiegamy trochę po torach przeszkód, ulicach miasta czy po świecie umarłych. Jak gra z początku jest kolorowa tak z czasem ustępuje bardziej stonowanym, mrocznym kolorom, że pod koniec mamy do czynienia z grą <abbr title='Gra robi się straszna, ale w bardzo łagodny sposób'>soft-koszmar</abbr>.</p>
                <img className='image' src={screen4} alt={"źródło: własne"} title={"Pierwszy większy boss, przerażający ale klimatyczny"}></img>
                <p lang='pl'>Z czasem rodzaj rozrywki również się zmienia. Plansze platformowe ustępują tym ganianym, żeby później przekształcić się tylko w walki z <abbr title='Duzi i mocniejsi przeciwnicy w słóżbie głownego złego'>bossami</abbr>. Tych mamy kilkoro: <abbr title='Pół-człowiek, pół-koń'>Centaur</abbr>, <abbr title='Człowiek z głową byka'>Minotaur</abbr>,  stado porywających ludzi ptaszysk, <abbr title='Smok któremu z odciętej głowy wyrastają kolejne'>Hydra</abbr>, <abbr title='Kobieta z wężami zamiast włosów, która zamienia wszystkie żywe stworzenia w kamień'>Meduza</abbr> i główny zły – <abbr title='Bóg zaświatów w mitologii greckiej'>Hades</abbr>.</p>
                <p lang='pl'>Teraz powiem Wam coś, czego większość graczy nie jest świadoma, gra jest BARDZO krótka, ukończyłem ją zaledwie w godzinę. Przez całą tę godzinę straciłem może z pięć żyć (większość na przedostatniej planszy). Walki z bossami są banalnie proste dla przeciętnego gracza, a ostatnia walka pokazuje że twórcom zabrakło pomysłów.</p>
                <img className='image' src={screen5} alt={"źródło: Własne"} title={"Plansze biegankowe, są tutaj częstrze, a im dalej w las tym jest trudniej"}></img>
                <p lang='pl'>Kolejnym problemem jest tutaj w pewnym stopniu sterowanie naszym herosem. Potężny Herkules nie radzi sobie niestety z otrzymywaniem obrażeń, gdyż po każdym trafieniu pojawia się sekundowa animacja która unieruchamia postać lub przerywa wykonywaną akcję.</p>
                <img className='image' src={screen6} alt={"źródło: Własne"} title={"Jedyna taka plansza w grze - do tego najtrudniejsza"}></img>
                <p lang='pl'>Ostatnim gwoździem do trumny jest system znajdziek, które tutaj nie mają żadnego sensu. Zbieramy litery które składają się w imię naszego bohatera, oraz specjalne wazy. Te ostatnie dają nam możliwość zapisania stanu gry, a pełny napis z tego co rozumiem odblokowuje możliwość ponownego uruchomienia planszy.</p>
                <img className='image' src={screen7} alt={"źródło: Własne"} title={"Przedostatnia plansza, najmroczniejsza i trudna"}></img>
                <p>Nostalgia do tej gry u mnie niestety przeminęła i nie będzie już tą, którą zapamiętałem z dzieciństwa. Nie jest ona tak długa jak zapamiętałem, nie jest ona również tak trudna jak utkwiła mi w pamięci. Graficznie do dzisiaj trzyma poziom, ale jeżeli chodzi o sterowanie, to wiek jej nie oszczędzał. Myślę że pomogłem Wam na odpowiedź na pytanie z początku tego tekstu.</p>
                <div className='tabela'>
                    <table className='tabelaInfo'>
                        <tr className='tabelaInfo'>
                            <td>Deweloper</td>
                            <td>Eurocom</td>
                        </tr>
                        <tr className='tabelaInfo'>
                            <td>Wydawca</td>
                            <td>Disney Int.</td>
                        </tr>
                        <tr className='tabelaInfo'>
                            <td>Data wydania</td>
                            <td>15 listopada 1998</td>
                        </tr>
                        <tr className='tabelaInfo'>
                            <td>Numer Seryjny</td>
                            <td>SCES-00819</td>
                        </tr>
                    </table>
                </div>
            </div>
            <ButtonGoToTop />
        </div>
    )
}

export default HerculesPage